import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { css } from "styled-components";

import { EngLoc } from "../../../assets/constants/localizations";

import { Title2 } from "../../../assets/styles/component";
import Slider from "./SliderDesktop";

import { smooth_right } from "../../../assets/styles/animations";

const SCasesInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  gap: 7.5rem;
  
  max-width: 100rem;

  
  & > div > div > div {
    opacity: 0;
    ${({ view }) => !view && smooth_right}
  }
`;

const CasesInner = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <SCasesInner id={"cases"} ref={elem} view={view}>
      <Title2>
        {language === "Ru" ? "Наши кейсы" : EngLoc.partners.cases.title}
      </Title2>
      <Slider language={language}>
      </Slider>
    </SCasesInner>
  );
};

export default CasesInner;
