import styled from "styled-components";
import { Title2, Title3 } from "../../assets/styles/component";
import CasesInner from "./Cases/CasesInnerDesktop";
import PartnersInner from "./Partners/PartnersInnerDesktop";

const SPartners = styled.section`
  padding: 9.375rem 8.3%;
  background: radial-gradient(
      25.59% 27.66% at 28.66% 55.7%,
      rgba(0, 174, 232, 0.75) 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      30.82% 30.82% at -3.18% 24.05%,
      rgba(0, 174, 232, 0.81) 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      42.33% 32.46% at 64.34% 83.23%,
      rgba(0, 174, 232, 0.91) 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      54.74% 77.61% at 104.76% 35.92%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      42.13% 26.82% at 7.81% 84.05%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      50.75% 50.75% at 61.35% 20.77%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    #000f67;

  & > div:first-child {
    margin-bottom: 11.25rem;
  }

  & h2 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
`;

const Partners = ({ language }) => {
  return (
    <SPartners>
      <PartnersInner language={language} />
      <CasesInner language={language} />
    </SPartners>
  );
};

export default Partners;
